import React, { PureComponent } from 'react'
import { Modal, Row, Col, Form, Button, message } from 'antd'
// import {CsxFormSelect, CsxFormSearch} from 'Form'
import { CsxFormSelect, CsxFormMultipleSelect } from 'Form'
import {
  API_TRANSFERASSOCIATIONS_LIST_NO_ASSOCIATION,
  API_TRANSFERASSOCIATIONS_TERMINAL_LOCATION_LIST
} from 'api'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const FormItem = Form.Item
@connect(
  state => ({ ...state.transferAssociation, userLoation: state.userLoation }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const { modalData, getSingleDet } = this.props
    if (modalData) {
      //编辑
      const { locationCode = '' } = modalData
      const params = {
        urlParams: [`${locationCode}`]
      }
      getSingleDet(params, data => {})
    }
  }
  handleSubmit = e => {
    const { form, handleAddOrEdit, dataLocationList, workType } = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      console.log('form values is', values)
      let reqObj = { ...values }
      if (!err) {
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'location') {
              let data = reqObj[i].children
              // reqObj.location = reqObj[i].children
              reqObj.locationCode = data && data.split(' ')[0]
              reqObj.locationName = data && data.split(' ')[1]
            }
          }
        }
        let result = {
            data: {
              ...reqObj,
              itemsList: dataLocationList || []
            }

          },
          msg = '创建成功'
        // 判断是否是修改,加ID
        if (workType && workType === 'edit') {
          result.data.workType = 'edit'
          msg = '编辑成功'
        }
        delete result.location
        delete result.locationList
        handleAddOrEdit({ ...result }, data => {
          message.success(msg)
          const { handleGetList, searchParams, page, size } = this.props
          handleGetList({ searchParams: { page, size, ...searchParams } })
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const { update } = this.props
    update({ isShowModal: false })
  }
  render() {
    const {
      modalTitle,
      update,
      addOneItemSelf,
      form: { getFieldDecorator },
      modalTuttonLoading,
      modalData = {}
    } = this.props
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 }
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        maskClosable={false}
        className='operationModal'
        footer={null}
        width='500px'
      >
        <Form onSubmit={this.handleSubmit} autoComplete='off'>
          <Row gutter={16}>
            <Col span={20}>
              <FormItem {...formItemLayout} label='工厂地点' hasFeedback>
                {getFieldDecorator('location', {
                  initialValue: modalData.location,

                  rules: [
                    {
                      required: true,
                      message: '请选择!'
                    }
                  ]
                })(
                  <CsxFormSelect
                    dateApi={API_TRANSFERASSOCIATIONS_LIST_NO_ASSOCIATION}
                    label='工厂地点'
                    addOneItemSelf={addOneItemSelf}
                    selectItem={{
                      key: 'code',
                      valueKey: ['code', 'name']
                    }}
                    onChangeSelect={data => {}}
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span={20}>
              <FormItem
                {...formItemLayout}
                label="调拨地点"
              >
                {getFieldDecorator('locationList', {
                  initialValue: modalData.locationList,
                  rules: [
                    {
                      required: true,
                      message: '请选择!'
                    }
                  ]
                })(
                  <CsxFormMultipleSelect
                    dateApi={API_TRANSFERASSOCIATIONS_TERMINAL_LOCATION_LIST}
                    noReady={false}
                    selectItem={{ key: 'code', valueKey: ['code', 'name'] }}
                    handleSelectChange = {data => {
                      const { children } = data
                      let listData = []
                      children && children.length > 0 && children.map((item) => {
                        const { props = [] } = item
                        const { value, children } = props
                        let reg = new RegExp(value)
                        listData.push({
                          transferLocationCode: value || '',
                          transferLocationName: children && children.replace(reg, '') || ''
                        })
                      })
                      update({
                        dataLocationList: listData
                      })
                    }}
                  />
                )}
              </FormItem>
            </Col>
          </Row>

          <Row type='flex' justify='center'>
            <Col>
              <Button
                style={{ marginRight: '48px' }}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type='primary'
                htmlType='submit'
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



