import * as actionType from './actionType'
import { getData } from 'util'
import {
  API_TRANSFERASSOCIATIONS_PAGE,
  API_TRANSFERASSOCIATIONS_LIST_NO_ASSOCIATION,
  API_TRANSFERASSOCIATIONS_DEL,
  API_TRANSFERASSOCIATIONS_CREATE,
  API_TRANSFERASSOCIATIONS_UPDATE,
  API_TRANSFERASSOCIATIONS_EXPORT,
  API_TRANSFERASSOCIATIONS_DETAIL
} from 'api'
//同步
export const update = object => ({
  type: actionType.TRANSFERASSOCIATIONS_UPDATE,
  data: object
})

// 获取 - 一键调拨地点关联列表
export const handleGetList = reqData => dispatch => {
  dispatch({
    type: actionType.TRANSFERASSOCIATIONS_UPDATE,
    data: { tableLoading: true }
  })
  const { searchParams = {} } = reqData
  for (let i in searchParams) {
    if (Object.prototype.toString.call(searchParams[i]) === '[object Object]') {
      searchParams[i] = searchParams[i].value
    }
  }
  getData({ api: API_TRANSFERASSOCIATIONS_PAGE, ...reqData })
  // getData({ api: API_TRANSFERASSOCIATIONS_PAGE1, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      let { page = '', size = '' } = reqData ? reqData.searchParams : {}
      const { result = {} } = data
      const { total = '', list = '' } = result
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_UPDATE,
        data: {
          page,
          size,
          total,
          tableLoading: false,
          searchResult: list
        }
      })
      dispatch(getTerminalList())
    })
    .catch(err => {
      console.log(err)
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_FAIL
      })
    })
}
// 获取 - 一键调拨地点列表数据
export const getTerminalList = (reqData, callBack) => dispatch => {
  getData({ api: API_TRANSFERASSOCIATIONS_LIST_NO_ASSOCIATION, ...reqData })
    .then(data => {
      callBack && callBack()
      const result = data && data.result
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_UPDATE,
        data: {
          factoryList: result
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_FAIL
      })
    })
}
// 删除 - 一键调拨数据
export const handleOperator = (reqData, callBack) => dispatch => {
  getData({ api: API_TRANSFERASSOCIATIONS_DEL, ...reqData })
    .then(data => {
      callBack && callBack()
    })
    .catch(err => {
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_FAIL
      })
    })
}
// 获取 - 单个一键调拨数据
export const getSingleDet = (reqData, callBack) => dispatch => {
  console.log('reqData is ', reqData)
  getData({ api: API_TRANSFERASSOCIATIONS_DETAIL, ...reqData })
    .then(data => {
      callBack && callBack(data)
      const { result } = data
      const {
        locationCode = '',
        locationName = '',
        itemsList = []
      } = result
      let locationList = []
      itemsList && itemsList.length && itemsList.map((item, index) => {
        const { transferLocationCode = '' } = item
        locationList.push(transferLocationCode)
      })
      console.log('locationList is', locationList)
      let dataFormater = {
        ...result,
        location: {
          value: `${locationCode} ${locationName}`,
          children: `${locationCode} ${locationName}`
        },
        locationList: {
          children: locationList
        }
      }
      console.log('dataFormater is', dataFormater)
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_UPDATE,
        data: {
          modalData: dataFormater,
          dataLocationList: itemsList
        }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_FAIL
      })
    })
}
// 创建/编辑 - 一键调拨数据
export const handleAddOrEdit = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.TRANSFERASSOCIATIONS_UPDATE,
    data: { modalTuttonLoading: true }
  })
  let api =
    reqData.data && reqData.data.workType && reqData.data.workType == 'edit'
      ? API_TRANSFERASSOCIATIONS_UPDATE
      : API_TRANSFERASSOCIATIONS_CREATE
  console.log('reqData is', reqData)
  getData({ api, ...reqData })
    .then(data => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_UPDATE,
        data: {
          modalTuttonLoading: false,
          isShowModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch(err => {
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_FAIL
      })
    })
}
// 导出
export const handleExport = (reqData, callBack) => dispatch => {
  dispatch({
    type: actionType.TRANSFERASSOCIATIONS_UPDATE,
    data: { modalTuttonLoading_handleExport: true }
  })
  getData({ api: API_TRANSFERASSOCIATIONS_EXPORT, ...reqData })
    .then(data => {
      callBack && callBack()
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
    .catch(err => {
      dispatch({
        type: actionType.TRANSFERASSOCIATIONS_UPDATE,
        data: { modalTuttonLoading_handleExport: false }
      })
    })
}



