import React, { PureComponent } from 'react'
import { Table } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
@connect(
  (state) => ({ ...state.transferAssociation, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '工厂地点',
        width: 100,
        dataIndex: 'locationCode',
        key: 'locationCode',
        render: (text, record) => {
          const { locationCode, locationName } = record
          return `${locationCode} ${locationName} `
        }
      },
      {
        title: '一键调拨地点关联',
        width: 250,
        dataIndex: 'itemsList',
        key: 'itemsList',
        render: (text, record, index) => {
          const lenOfCode = text && text.length
          let strCode = ''
          text &&
            lenOfCode &&
            text.map((item = {}, idx, self) => {
              const { transferLocationCode, transferLocationName } = item
              strCode += `${transferLocationCode}${transferLocationName}${
                idx != lenOfCode - 1 ? '、' : ''
              }`
            })
          const len = strCode && strCode.length

          return len > 50 ? `${strCode.substring(0, 50)}...` : strCode
        }
      },
      {
        title: '最后更新时间',
        width: 180,
        dataIndex: 'updateTime',
        key: 'updateTime'
      },
      {
        title: '最后更新人',
        width: 120,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '操作',
        width: 100,
        dataIndex: 'x',
        key: 'x',
        // fixed: 'right',
        render: (text, record, index) => (
          <div className="tableOperation">
            {this.renderBtn(text, record, index)}
          </div>
        )
      }
    ]
  }

  renderBtn(text, record, index) {
    const { menuPermissionIds } = this.props
    let dom = (
      <span
        className={
          menuPermissionIds &&
          menuPermissionIds.includes('transferAssociation_edit')
            ? 'blue'
            : 'disabledBtnInTable'
        }
        onClick={() => {
          if (menuPermissionIds.includes('transferAssociation_edit')) {
            this.onEdit(text, record, index)
          }
        }}
      >
        编辑
      </span>
    )
    return dom
  }
  onEdit(text, record, index) {
    const { update } = this.props
    const { locationCode, locationName } = record
    const addOneItemSelf = { code: locationCode, name: locationName }
    update({
      isShowModal: true,
      modalTitle: '调拨地点关联',
      modalData: record,
      workType: 'edit',
      addOneItemSelf
    })
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue =
      (newProps && newProps.userLoation && newProps.userLoation.value) || ''
    const userLoationValue = (userLoation && userLoation.value) || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({
        searchParams: {
          page: 1,
          size: newProps.size,
          ...newProps.searchParams
        }
      })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      selectedRowKeys,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      }
    }
    return (
      <div className="table-group">
        <Table
          rowKey={(record) => record.locationCode}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult || []}
          // scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



